import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { string } from '@amcharts/amcharts4/core';

@Component({
  selector: 'kt-create-site',
  templateUrl: './create-site.component.html',
  styleUrls: ['./create-site.component.scss']
})
export class CreateSiteComponent implements OnInit {
  code : string;
  description : string;
  entite: string;
  declar: string;
  status: string;
  cetrans: string;



  siteForm: FormGroup;
  hasFormErrors: boolean = false;
  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private siteFB: FormBuilder
  ) { }



  ngOnInit() {
    this.initsiteForm();
  }

  initsiteForm() {
  
    this.siteForm = this.siteFB.group({
      code: [this.code, Validators.required],
      description: [this.description, Validators.required],
      entite: [this.entite, Validators.required],
      declar: [this.declar, Validators.required],
      status: [this.status, Validators.required],
      cetrans: [this.cetrans, Validators.required],
    
        })
  }


}
