import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { string } from '@amcharts/amcharts4/core';


@Component({
  selector: 'kt-create-loc',
  templateUrl: './create-loc.component.html',
  styleUrls: ['./create-loc.component.scss']
})
export class CreateLocComponent implements OnInit {

  site : string;
  empl : string; 
  description : string;
  status: string;
  projet: string;
  type: string;

  

  emplForm: FormGroup;
  hasFormErrors: boolean = false;
  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private emplFB: FormBuilder
  ) { }



  ngOnInit() {
    this.initemplForm();
  }

  initemplForm() {
  
    this.emplForm = this.emplFB.group({
      site: [this.site, Validators.required],
      empl: [this.empl, Validators.required],
      description: [this.description, Validators.required],
      status: [this.status, Validators.required],
      projet: [this.projet, Validators.required],
      type:   [this.type, Validators.required],
    
        })
  }



}
