// Angular

import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { InventorySettingComponent } from './inventory-setting.component';
import { CreateSiteComponent } from './create-site/create-site.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgbDate, NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { DatePipe } from '@angular/common'
import { PartialsModule } from '../../partials/partials.module';
import { MaterialPreviewModule } from '../../partials/content/general/material-preview/material-preview.module';
import { CreateLocComponent } from './create-loc/create-loc.component';
import { CreateStatusStockComponent } from './create-status-stock/create-status-stock.component';




const routes: Routes = [
  {
    path: 'create-site',
    component: CreateSiteComponent
  },
  {
    path: 'create-loc',
    component: CreateLocComponent
  },
  {
    path: 'create-status-stock',
    component: CreateStatusStockComponent
  },
]  

@NgModule({

  imports: [
    CommonModule,
    HttpClientModule,
    FormsModule,
    RouterModule.forChild(routes),
    ReactiveFormsModule,
    PartialsModule,
    MaterialPreviewModule,
    NgbModule
  ],
  exports: [RouterModule],
  declarations: [InventorySettingComponent, CreateSiteComponent, CreateLocComponent, CreateStatusStockComponent]
})
export class InventorySettingModule { }
